import React, { ChangeEvent, DetailedHTMLProps, InputHTMLAttributes, KeyboardEvent, ReactNode } from "react";
import s from "common/components/superComponents/superInputText/SuperInputText.module.scss";

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement>

//               ,  type
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperInputTextPropsType = Omit<DefaultInputPropsType, "type"> & {
  //  +       
  onChangeText?: (value: string) => void
  onEnter?: () => void
  error?: ReactNode
  spanClassName?: string
}

const SuperInputText: React.FC<SuperInputTextPropsType> = (
  {
    onChange,
    onChangeText,
    onKeyPress,
    onEnter,
    error,
    className,
    spanClassName,
    id,

    ...restProps //       restProps
  }
) => {
  const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
    onChange?.(e); //    onChange,     ( onChange  )

    onChangeText?.(e.currentTarget.value);
  };
  const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
    onKeyPress?.(e);

    onEnter && //    onEnter
    e.key === "Enter" && //     Enter
    onEnter(); //   
  };

  const finalSpanClassName = s.error
    + (spanClassName ? " " + spanClassName : "");
  const finalInputClassName = s.input + (className ? " " + className : ""); //    
// + (error ? " " + s.errorInput : " " + s.superInput)
  return (
    <div className={s.inputWrapper}>
            <span
              id={id ? id + "-span" : undefined}
              className={finalSpanClassName}
            >
                {error}
            </span>
      <input
        id={id}
        type={"text"}
        onChange={onChangeCallback}
        onKeyPress={onKeyPressCallback}
        className={finalInputClassName}
        {...restProps} //        (value   )
      />
    </div>
  );
};

export default SuperInputText;
